<template>
  <v-text-field v-model="whpassvalue" v-bind="$attrs" :rules="passrules" v-on="$listeners" :inheritAttrs="false"
    @focus="focusAction" @blur="blurAction" />
</template>

<script>
  export default {
    name: 'wh-moneyfield',
    model: {
      prop: 'value',
      event: 'whvalueChanged'
    },
    props: {
      value: {
        type: String,
        default () {
          return ''
        }
      },
      rules: {
        type: Array,
        default () {
          return []
        }
      }
    },
    data() {
      return {
        cacheValue: '',
        isFocusing: false,
        rulesTag: {
          ruleMoney: value => {
            const pattern = /^(\-|\+)?\d+(\.\d+)?$/
            return pattern.test(value) || 'Invalid Money.'
          }
        }
      }
    },
    computed: {
      whpassvalue: {
        get() { // 
          return this.isFocusing ? this.value : this.priceSwitch(this.value)
        },
        set(val) { //
          if (this.isFocusing) {
            this.cacheValue = val
          } else {
            this.cacheValue = val
            this.$emit('whvalueChanged', this.cacheValue)
          }
        }
      },
      passrules() {
        return this.rules.concat(this.isFocusing ? [this.rulesTag.ruleMoney] : []);
      }
    },
    methods: {
      priceSwitch(x) {
        //
        var f = parseFloat(x);
        if (isNaN(f)) return '0.00';
        var f = Math.round(x * 100) / 100;
        var s = f.toString();
        var rs = s.indexOf('.');
        if (rs < 0) {
          rs = s.length;
          s += '.';
        }
        while (s.length < (rs + 1) + 2) {
          s += '0';
        }
        //
        var leftNum = s.split(".")[0];
        var rightNum = "." + s.split(".")[1];
        var result;
        //
        var resultArray = new Array();
        if (leftNum.length > 3) {
          var i = true;
          while (i) {
            resultArray.push(leftNum.slice(-3));
            leftNum = leftNum.slice(0, leftNum.length - 3);
            if (leftNum.length < 4) {
              i = false;
            }
          }
          //
          var sortArray = new Array();
          for (var i = resultArray.length - 1; i >= 0; i--) {
            sortArray.push(resultArray[i]);
          }
          result = leftNum + "," + sortArray.join(",") + rightNum;
        } else {
          result = s;
        }
        return result;
      },
      focusAction(e) {
        //console.log('focused.....', e)
        this.isFocusing = true
      },
      blurAction(e) {
        //console.log('blurred.....', e)
        this.isFocusing = false
        //console.log('before the cachevalue', this.cacheValue)
        var f = parseFloat(this.cacheValue);
        if (isNaN(f) || f === null) {
          f = 0;
        }
        // 
        f = Math.round(f * 100) / 100;
        this.cacheValue = f + ''
        //console.log('now the cachevalue', this.cacheValue)
        this.$emit('whvalueChanged', this.cacheValue)
      }
    }
  }
</script>