<template>
  <v-dialog ref="dialog" v-model="modal" :return-value.sync="whpassvalue" :disabled="readonly" persistent full-width width="290px">
    <template v-slot:activator="{ on }">
      <v-text-field v-model="whpassvalue" :label="label" append-icon="mdi-event" readonly v-on="on">
      </v-text-field>
    </template>
    <v-time-picker v-model="whpassvalue" :readonly="readonly" scrollable>
      <v-spacer></v-spacer>
      <v-btn text color="primary" @click="modal = false"></v-btn>
      <v-btn text color="primary" @click="$refs.dialog.save(whpassvalue)"></v-btn>
    </v-time-picker>
  </v-dialog>
</template>
<script>
  export default {
    name: 'wh-time-picker',
    model: {
      prop: 'time',
      event: 'whtimeChanged'
    },
    data: () => ({
      menu: false,
      modal: false
    }),
    props: {
      time : '',
      label : '',
      readonly : false
    },
    computed: {
      whpassvalue: {
        get() { // 
          return this.time
        },
        set(val) { //
          this.$emit('whtimeChanged', val)
        }
      }
    }
  }
</script>